


































import {
  computed, defineComponent, PropType, Ref,
} from '@vue/composition-api';
import {
  CheckboxToggleEditorControl,
  ColorBoxEditorControl,
  ComboBoxEditorControl,
} from '@/app/editor/client/view/component/control';
import { Color } from '@/app/shared/client/type/color';
import { GlobalDesign } from '@/app/shared/client/type/tile/global';
import { calculateActualColorDefault } from '@/app/editor/client/logic/defaults';
import useI18N from '@/app/shared/client/logic/i18n';
import {
  SocialIconListAppearance, SocialIconListDefaults,
  SocialIconListSettings,
} from '@/app/shared/client/type/settings/social-icon-list-settings';
import { socialIconListAppearanceLabel } from '@/app/shared/client/helper/social-icon-list';

function useAppearanceChangeHandler(
  emit: (event: string, arg: SocialIconListSettings) => void,
  settings: Ref<Readonly<SocialIconListSettings>>,
): (value?: SocialIconListAppearance) => void {
  function changeAppearance(value?: SocialIconListAppearance): void {
    const newSettings = {
      ...settings.value,
      appearance: value,
    };
    emit('editor-action', newSettings);
  }

  return changeAppearance;
}

function useColorChangeHandler(
  emit: (event: string, arg: SocialIconListSettings) => void,
  settings: Ref<Readonly<SocialIconListSettings>>,
): (value?: Color) => void {
  function changeColor(value?: Color): void {
    const newSettings = {
      ...settings.value,
      color: value,
    };
    emit('editor-action', newSettings);
  }

  return changeColor;
}

function useVisibilityChangeHandler(
  emit: (event: string, arg: SocialIconListSettings) => void,
  settings: Readonly<Ref<Readonly<SocialIconListSettings>>>,
): (value?: boolean) => void {
  function changeVisibility(value?: boolean): void {
    const newSettings = {
      ...settings.value,
      visible: value,
    };
    emit('editor-action', newSettings);
  }

  return changeVisibility;
}

const SocialIconListDesignEditor = defineComponent({
  components: {
    ComboBoxEditorControl,
    ColorBoxEditorControl,
    CheckboxToggleEditorControl,
  },
  props: {
    value: {
      required: false,
      default: undefined,
      type: Object as PropType<SocialIconListSettings>,
    },
    defaults: {
      required: true,
      type: Object as PropType<SocialIconListDefaults>,
    },
    siteSettings: {
      required: false,
      default: undefined,
      type: Object as PropType<GlobalDesign>,
    },
    colorList: {
      required: true,
      type: Array as PropType<string[]>,
    },
    autoColorEnabled: {
      required: true,
      type: Boolean,
    },
    alphaColorEnabled: {
      required: true,
      type: Boolean,
    },
  },
  setup(props, { root, emit }) {
    const i18n = useI18N(root.$store);

    const settings = computed(() => ({
      appearance: props?.value?.appearance,
      color: props.value?.color,
      visible: props.value?.visible ?? props.defaults.visible,
    }));

    const appearance = computed({
      get: () => settings.value.appearance,
      set: useAppearanceChangeHandler(emit, settings),
    });
    const appearanceChoices = computed(() => {
      const defaultAppearance = props.defaults.appearance as SocialIconListAppearance;
      const defaultAppearanceText = i18n('CommonLabels.SocialIconList.Appearance.Default')
        .replace('{appearanceType}', i18n(socialIconListAppearanceLabel(defaultAppearance)));

      return [
        { key: undefined, label: defaultAppearanceText },
        {
          key: SocialIconListAppearance.PLAIN,
          label: i18n(socialIconListAppearanceLabel(SocialIconListAppearance.PLAIN)),
        },
        {
          key: SocialIconListAppearance.SOLID_CIRCLE,
          label: i18n(socialIconListAppearanceLabel(SocialIconListAppearance.SOLID_CIRCLE)),
        },
        {
          key: SocialIconListAppearance.OUTLINE_CIRCLE,
          label: i18n(socialIconListAppearanceLabel(SocialIconListAppearance.OUTLINE_CIRCLE)),
        },
      ];
    });

    const defaultColor = computed(() => calculateActualColorDefault(
      props.defaults.color,
      props.siteSettings,
    ));
    const color = computed({
      get: () => settings.value.color,
      set: useColorChangeHandler(emit, settings),
    });

    const visibility = computed({
      get: () => settings.value.visible,
      set: useVisibilityChangeHandler(emit, settings),
    });

    return {
      i18n,
      appearance,
      appearanceChoices,
      defaultColor,
      color,
      visibility,
    };
  },
});

export default SocialIconListDesignEditor;
